@import './library.scss';

.modal { 
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.801);
}

.modal-content {
  opacity: 1;
  width: 60%;
  height: 60%;
  // background-color: rgba(255, 255, 255, 0.308);
}

.modal-header {
  text-align: right;
}

#close-btn {
  border: none;
  background-color: transparent;
  color: $font-color;
  &:hover, &:focus {
    color: $secondary-blue;
    cursor: pointer;
  }
}

.close-btn {
  font-size: 50px;
  &:hover {
    color: $secondary-blue;
    cursor: pointer;
  }
}

.modal-body {
  margin-top: 50px;
  text-align: center;
  font-size: 30px;
}

@media only screen and (max-width: 500px) {
  .modal-body, .close-btn {
    font-size: large;
  }
}
